import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable()
export class GlobalProvider {
  categories: [any] = [{
    name: "BreakFast",
    imageURL: "breakfast.jpg",
    itemsNumber: "20",
    pageURL: "BreakfastPage"
  },
  {
    name: "Diner",
    imageURL: "diner.jpg",
    itemsNumber: "64",
    pageURL: "DinerPage"
  },
  {
    name: "Sweets",
    imageURL: "sweets.jpg",
    itemsNumber: "20",
    pageURL: "SweetsPage"
  },
  {
    name: "Coffee",
    imageURL: "coffee.jpg",
    itemsNumber: "28",
    pageURL: "CoffeePage"
  }];
  constructor(public http: Http) {
    
  }

  getCategories(){
    return this.categories;
  };
}



